@charset "UTF-8";
*,
:before,
:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  background: #fff;
  font: 400 19px/28px "Fira Sans", sans-serif;
  color: #3d4140;
}

a {
  color: #5a97cb;
  outline: none;
  text-decoration: underline;
}

a:hover,
a:active {
  text-decoration: none;
}

p {
  margin: 0 0 10px;
}

img {
  display: block;
  max-width: 100%;
}

b,
strong {
  font-weight: 700;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Fira Sans", sans-serif;
  font-weight: 600;
  color: #0a1828;
  line-height: normal;
  margin: 0 0 15px;
}

h1 {
  font-size: 46px;
  line-height: 59px;
  font-weight: 700;
}

h2 {
  font-size: 40px;
  line-height: 30px;
  font-weight: 700;
}

h3 {
  font-size: 31px;
  line-height: 26px;
}

h4 {
  font-size: 30px;
  line-height: 30px;
  font-weight: 400;
}

h5 {
  font-size: 28px;
  line-height: 30px;
  font-weight: 700;
}

h6 {
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0 0 10px;
}

ul.marker li,
ol.marker li {
  padding-left: 17px;
  position: relative;
}

ul.marker li:before {
  content: "";
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #232625;
  display: block;
  position: absolute;
  left: 0;
  top: 12px;
}

ol.marker li {
  counter-increment: step-counter;
}

ol.marker li:before {
  content: counter(step-counter) ".";
  position: absolute;
  left: 0;
  top: 0;
  font-size: 19px;
  line-height: 28px;
  color: #3d4140;
}

q {
  display: block;
  margin: 0 0 10px;
  padding: 20px 0;
  font-size: 16px;
  line-height: 28px;
  font-weight: 300;
  font-style: italic;
  color: #3d4140;
}

svg {
  display: block;
  overflow: hidden;
}

input,
textarea,
select,
button {
  outline: none;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #888f98;
  opacity: 1;
  /* Хром */
}

input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #888f98;
  opacity: 1;
  /* Файерфокс */
}

input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #888f98;
  opacity: 1;
  /* Для старых версий файерфокса */
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #888f98;
  opacity: 1;
  /* едж и эксплорер */
}

input::-ms-clear {
  display: none;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.cover {
  padding: 0 15px;
  position: relative;
  margin: 0 auto;
}
@media screen and (min-width: 480px) {
  .cover {
    min-width: 320px;
  }
}
@media screen and (min-width: 600px) {
  .cover {
    max-width: 570px;
  }
}
@media screen and (min-width: 768px) {
  .cover {
    max-width: 730px;
  }
}
@media screen and (min-width: 990px) {
  .cover {
    max-width: 970px;
  }
}
@media screen and (min-width: 1200px) {
  .cover {
    max-width: 1170px;
  }
}
@media screen and (min-width: 1440px) {
  .cover {
    max-width: 1390px;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100%;
  overflow: hidden;
}
.wrapper__content {
  flex: 1 0 auto;
}
.wrapper__footer {
  flex: 0 0 auto;
}

.jq-selectbox.modal-filter__select .jq-selectbox__select {
  border: 2px solid #e1e4ec;
  border-radius: 2px;
  box-shadow: none;
  height: 75px;
  background: none;
  padding: 0 45px 0 20px;
  font: 400 22px/30px "Fira Sans", sans-serif;
  color: #2c2f2e;
}
.jq-selectbox.modal-filter__select .jq-selectbox__trigger {
  border: none;
}
.jq-selectbox.modal-filter__select .jq-selectbox__trigger-arrow {
  width: 9px;
  height: 9px;
  top: 30px;
  left: 2px;
  right: auto;
  border: none;
  border-bottom: 2px solid #a3abb5;
  border-right: 2px solid #a3abb5;
