}
.jq-selectbox.modal-filter__select .jq-selectbox__select-text {
  margin-top: 22px;
}
.jq-selectbox.modal-filter__select .jq-selectbox__select-text.placeholder {
  color: #a3abb5;
}
.jq-selectbox.modal-filter__select .jq-selectbox__dropdown {
  border: 2px solid #e1e4ec;
  border-radius: 2px;
  box-shadow: none;
  margin-top: -2px;
  font: inherit;
}
.jq-selectbox.modal-filter__select .jq-selectbox__dropdown ul li {
  padding: 9px 20px 10px;
}

.jq-selectbox.modal-filter__select.changed .jq-selectbox__select-text {
  margin-top: 31px;
}

.jq-selectbox.modal-filter__sl-currency .jq-selectbox__select {
  background: #e1e4ec;
  border-radius: 0px 2px 2px 0px;
  height: 41px;
  width: 67px;
  border: none;
  box-shadow: none;
  font: 400 20px/22px "Fira Sans", sans-serif;
  color: #064f92;
  padding: 0 40px 0 15px;
}
.jq-selectbox.modal-filter__sl-currency .jq-selectbox__select-text {
  min-width: 12px;
  padding-top: 10px;
}
.jq-selectbox.modal-filter__sl-currency .jq-selectbox__trigger {
  border: none;
}
.jq-selectbox.modal-filter__sl-currency .jq-selectbox__trigger-arrow {
  right: 16px;
  top: 16px;
  border: none;
  width: 7px;
  height: 7px;
  border-bottom: 1px solid #a3abb5;
  border-right: 1px solid #a3abb5;
  transform: rotate(45deg);
}
.jq-selectbox.modal-filter__sl-currency .jq-selectbox__dropdown {
  font: inherit;
  border: none;
}

.modal-filter__sl-strip .irs-line {
  top: 25px;
  height: 5px;
  background-color: #ddd;
  border-radius: 2.5px;
}
.modal-filter__sl-strip .irs-bar {
  height: 5px;
  background-color: #064f92;
  border-radius: 2.5px;
}
.modal-filter__sl-strip .irs-handle {
  width: 30px;
  height: 31px;
  background-color: #064f92;
  border: 7px solid #ffffff;
  box-shadow: 0px 1px 8px 0px rgba(0, 0, 0, 0.2);
  border-radius: 50%;
