.social {
  margin: 0 -5px;
  display: flex;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .social {
    justify-content: stretch;
  }
}
.social__item {
  padding: 0 5px;
}
.social__link {
  display: block;
  height: 29px;
  width: 29px;
}
.social__icon {
  max-width: none;
  width: 100%;
}

.pull {
  flex: 0 0 auto;
  display: flex;
}
@media screen and (min-width: 990px) {
  .pull {
    display: none;
  }
}
.pull__wrapper {
  display: flex;
  cursor: pointer;
  flex: 0 0 auto;
  align-items: center;
}
.pull__icon {
  width: 20px;
  height: 17px;
  flex: 0 0 auto;
  position: relative;
}
.pull__icon div {
  height: 2px;
  width: 100%;
  background: #fff;
  position: absolute;
  left: 0;
  transition: all 200ms ease;
}
.pull__icon div:nth-child(1) {
  top: 0;
}
.pull__icon div:nth-child(2) {
  top: 7px;
}
.pull__icon div:nth-child(3) {
  top: 14px;
}
.pull__text {
  font: 500 17px "Fira Sans", sans-serif;
  color: #fff;
  text-transform: uppercase;
  display: none;
  margin-left: 15px;
}
@media screen and (min-width: 480px) {
  .pull__text {
    display: block;
  }
}

