  background: #f43c2e;
  box-shadow: 0px 1px 5px 1px rgba(75, 75, 75, 0.1);
  border-radius: 2px;
  border: none;
  padding: 20px 37px 16px;
  cursor: pointer;
  transform: all 0.3s ease;
}
.modal-filter__btn-input:hover, .modal-filter__btn-input:active {
  background: #064f92;
}
.modal-filter__close-btn {
