.modal-close__link {
  display: block;
  text-decoration: none;
  font: 400 18px/18px "Fira Sans", sans-serif;
  color: #eeeeee;
  padding-left: 25px;
  transition: all 0.2s ease;
}
.modal-close__link:hover, .modal-close__link:active {
  color: #E83F33;
}
.modal-close__link:hover:before, .modal-close__link:hover:after, .modal-close__link:active:before, .modal-close__link:active:after {
  background: #E83F33;
}
.modal-close__link:before, .modal-close__link:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 8px;
  width: 17px;
  height: 2px;
  background: #eeeeee;
  transform: rotate(45deg);
  transition: all 0.3s ease;
}
.modal-close__link:after {
  transform: rotate(-45deg);
}

.category {
  position: relative;
  background: #fff;
  border: 2px solid #e7e9ef;
  border-radius: 2px;
  height: 100%;
  min-height: 187px;
}
.category:not(.category_noimg):hover .category__name, .category:not(.category_noimg):active .category__name {
  opacity: 0;
}
.category:hover .category__links, .category:active .category__links {
  opacity: 1;
  visibility: visible;
}
.category_noimg:hover .category__all-categories, .category_noimg:active .category__all-categories {
  color: #E83F33;
}
.category_noimg:hover .category__all-categories:before, .category_noimg:active .category__all-categories:before {
  border-bottom: 2px solid #E83F33;
  border-right: 2px solid #E83F33;
}
.category_noimg .category__name {
  display: flex;
  align-items: center;
  justify-content: center;
}
.category__name {
  display: block;
  opacity: 1;
